.header {
  min-height: 132px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #A5A5A5;
  font-size: 36px;
  font-weight: 900;
  color: white;
  margin-bottom: 40px;
}

@media screen and (max-width: 1050px) {
  .header {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }
  .header__button {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

.header__button {
  border: 1px solid #FFFFFF;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 100px;
  background: transparent;
  color: #FFFFFF;
  cursor: pointer;
  height: 48px;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding: 12px;
  font-family: 'Roboto', sans-serif;
}

.header__text {
  font-family: Permanent Marker, cursive;
  font-size: 80px;
  font-weight: 900;
  margin: 0;
}


